import React from 'react';
import { Box, Typography, Button, Container } from '@mui/material';
import bgImg from '../images/hero-bg.png';

const useStyles = {
  joinBox: {
    border: '2px solid #313741',
    borderRadius: '8px',
    marginTop: "20%",
    height: "auto",
    padding: '16px',
    textAlign: 'center',
    boxShadow: 3,
    position: 'relative',
    overflow: 'hidden',
  },
  backgroundImage: {
    width: '100%',
    height: '250px',
    position: 'absolute',
    top: 0,
    left: 0,
    zIndex: -1,
    opacity: 0.5,
  },
  joinText: {
    position: 'relative',
    zIndex: 1,
    color: 'white',
  },
  joinButton: {
    mt: 10,
    // width: "15%",
    padding:"15px 50px",
    borderRadius: "30px",
    background: "linear-gradient(to right, #6CB1FC, #B31CF1)",  // Updated line
    transition: 'background-color 0.5s ease',
    '&:hover': { 
      backgroundColor: "#363336" 
    },
  },
};

const JoinBox = () => {
  return (
    <Container>
      <Box sx={useStyles.joinBox}>
        <img src={bgImg} alt="Background" style={useStyles.backgroundImage} />
        <Typography variant="h6" component="p" gutterBottom sx={useStyles.joinText} style={{fontWeight:"bold"}} >
          Join Today For A <br />Prosperous Tomorrow
        </Typography>
        <Button variant="contained" sx={useStyles.joinButton}>
          Join Us
        </Button>
      </Box>
    </Container>
  );
};

export default JoinBox;

