import React from 'react';
import { Box, Typography, useMediaQuery, useTheme } from '@mui/material';
import { Bar } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';

Chart.register(...registerables);

const MyComponent = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  // Define data for your chart with different styles for each bar
  const data = {
    labels: [
      'Buyback and burn of $Minex Automatic',
      'Community Incentives',
      'Partnership',
      'Operation and Management'
    ],
    datasets: [
      {
        label: 'Funds allocation',
        data: [100, 40, 40, 20],
        backgroundColor: ['orange', 'blue', 'green', 'red'], // Different background colors
        borderWidth: 1,
        barThickness: 20, // Set the bar thickness
        maxBarThickness: 40, // Set the maximum bar thickness
      }
    ]
  };

  const options = {
    indexAxis: 'y',
    plugins: {
      shadowPlugin: true
    }
  };

  return (
    <Box
      display={"flex"}
      flexDirection={{ xs: 'column', sm: 'column', md: 'row', lg: 'row' }}
      justifyContent="space-between"
      alignItems={isSmallScreen ? 'flex-start' : 'center'}
      height="auto"
      p={2}
    >
      <Box
        flex={1}
        display="flex"
        flexDirection="column"
        justifyContent="center"
        p={2}
        sx={{
          width: isSmallScreen ? '100%' : 'auto'
        }}
      >
        <Typography variant="h4">Buyback and Burn to significant growth</Typography>
        <Typography
          variant="body1"
          paragraph
          sx={{
            paddingTop: "20px",
            fontSize: "12px",
            width: isSmallScreen ? '80%' : '90%',
            color: 'rgba(221, 221, 221, 1)',
            textAlign: isSmallScreen ? 'left' : 'center'
          }}
        >
          A continuous process of buying back and burning tokens has been proven to be an effective strategy for achieving substantial growth. Our Bitcoin mining utility creates a self-sustaining income stream that supports the growth of this token and helps establish a stable price.
        </Typography>
      </Box>
      <Box
        flex={1}
        p={2}
        display="flex"
        justifyContent="center"
        sx={{
          width: isSmallScreen ? '90%' : '90%'
        }}
      >
        <Box sx={{ width: isSmallScreen ? '90%' : '90%' , boxShadow: '0 10px 20px rgba(0, 0, 0, 0.3)', // Add shadow here
          borderRadius: '8px', // Optional: add border-radius for rounded corners
          border:"2px solid #313741",}}>
          <Bar data={data} options={options} sx={{ color: 'orange' }} />
        </Box>
      </Box>
    </Box>
  );
};

export default MyComponent;
