import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import loginImage from '../images/Icon.png';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Box from '@mui/material/Box';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import Mylogo from '../images/minelogo.png';
import Menuicon from '../images/Frame.png'

const Navbar = () => {
  const theme = useTheme();
  const isBelow1205px = useMediaQuery('(max-width:1240px)');
  const [drawerOpen, setDrawerOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setDrawerOpen(true);
  };

  const handleDrawerClose = () => {
    setDrawerOpen(false);
  };

  const menuItems = ['About us', 'Whitepaper', 'FAQ', 'News & Updates', 'Community', 'Support'];

  return (
    <AppBar position="fixed" style={{ backgroundColor: "#1f232c" }}>
      <Toolbar>
        <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
          <img src={Mylogo} style={{width:"150px",paddingTop:"10px",marginLeft:"15px"}} alt="Mylogo" />
        </Typography>

        {isBelow1205px ? (
          <>
            <IconButton
              edge="end"
              color="inherit"
              aria-label="menu"
              onClick={handleDrawerOpen}
              sx={{ marginLeft: 'auto' }}
            >
              <img src={Menuicon} alt="Menu" style={{ marginRight: '8px',  width: '30px' }} />
            </IconButton>
            <Drawer
            
            anchor="right"
            open={drawerOpen}
            onClose={handleDrawerClose}
            sx={{
              '& .MuiPaper-root': {
                backgroundColor: '#1b1e23', // Change the color here
                color: 'white', // Optional: change text color to make it readable on dark background
              },
            }}
              
            >
              <Box
                sx={{ width: 250 }}
                role="presentation"
                onClick={handleDrawerClose}
                onKeyDown={handleDrawerClose}
              >
                <List>
                  {menuItems.map((item, index) => (
                    <ListItem button key={index}>
                      <ListItemText primary={item} />
                    </ListItem>
                  ))}
                </List>
                <List>
                  <ListItem button>
                  <Button
      color="inherit"
      sx={{
        border: '1px solid #313741',
        color: 'white',
        borderRadius: '20px',
        paddingLeft: '25px',
        paddingRight: '25px',
        '&:hover': { backgroundColor: 'gray' },
        marginRight: '20px'
      }}
    >
      <img src={loginImage} alt="Login" style={{ marginRight: '8px', height: '20px', width: '20px' }} />
      Login
    </Button>
                  </ListItem>
                  <ListItem button>
                  <Button
              color="inherit"
              sx={{
                backgroundColor: "white",
                fontFamily:"Poppins",
                fontWeight:"600",
                fontSize:"16px",
                color: "black",
                borderRadius: "20px",
                paddingLeft: "25px",
                paddingRight: "25px",
                "&:hover": { backgroundColor: "white" }
              }}
            >
              Get Started
            </Button>
                  </ListItem>
                </List>
              </Box>
            </Drawer>
          </>
        ) : (
          <>
            <Box sx={{ display: 'flex', flexGrow: 1, justifyContent: 'center' ,marginRight:"8%"}}>
              {menuItems.map((item, index) => (
                <Button key={index} color="inherit">{item}</Button>
              ))}
            </Box>
   <Button
      color="inherit"
      sx={{
        border: '1px solid #313741',
        color: 'white',
        borderRadius: '20px',
        paddingLeft: '25px',
        paddingRight: '25px',
        '&:hover': { backgroundColor: 'gray' },
        marginRight: '20px'
      }}
    >
      <img src={loginImage} alt="Login" style={{ marginRight: '8px', height: '20px', width: '20px' }} />
      Login
    </Button>

            <Button
              color="inherit"
              sx={{
                backgroundColor: "white",
                fontFamily:"Poppins",
                fontWeight:"600",
                fontSize:"16px",
                color: "black",
                borderRadius: "20px",
                paddingLeft: "25px",
                paddingRight: "25px",
                "&:hover": { backgroundColor: "white" }
              }}
            >
              Get Started
            </Button>
          </>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
