import React from 'react';
import { Grid, Box, Typography } from '@mui/material';
import bgImg from '../images/hero-bg.png';
import Calender from '../images/calender.png';
import GrowArrow from '../images/grow.png';
import Buy from '../images/ok.png';
import Glob from '../images/world.png';
import Mans from '../images/human.png';
import rightImg from '../images/rightcolor.png';
import leftImg from '../images/leftcolor.png';


const useStyles = {
  backgroundImage: {
    width: '100%', 
    height: '100%',
    position: 'absolute',
    top: 0,
    left: 0,
    zIndex: -1, 
    opacity: 0.5, 
  },
}

const Boxes = () => {
  return (
    <div>
      <Box sx={{ position: 'relative', width: '100%', overflow: 'hidden' }}>
      <img 
        src={leftImg} 
        alt="Left Background" 
        style={{
          position: 'absolute',
          top: '0px',
          left: '0',
          width: '95%', // Adjust size as needed
          height: '100%',
          zIndex: 1,
          opacity: 1 // Optional: adjust opacity for better text visibility
        }}
      />
      <img 
        src={rightImg} 
        alt="Right Background" 
        style={{
          position: 'absolute',
          top: '0',
          right: '0',
          width: '100%', // Adjust size as needed
          height: '100%',
          zIndex: 1,
          opacity: 1 // Optional: adjust opacity for better text visibility
        }}
      />

     <Box
        sx={{
          position: 'relative',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          height: '300px',
          overflow: 'hidden',
          '@media (max-width: 600px)': {
            height: 'auto',
          },
        }}
      >
        <Box
          component="img"
          src={bgImg}
          alt="Background"
          style={useStyles.backgroundImage}          
        />
        <Grid
          container
          spacing={{ xs: 2, md: 3 }}
          justifyContent="center"
          sx={{
            width: '100%',
            textAlign: 'center', 
          }}
        >
          <Grid item xs={6} sm={6} md={4} lg={2}>
            <Box
              sx={{
                border: '1px solid #ddd',
                padding: 2,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                borderRadius: 1,
                background: 'linear-gradient(to right,#90a1c4, #3e3154)',
                color: "black",
                textAlign: 'center',
                margin: "10px 5px"
              }}
            >

              <img src={Calender} alt=""  style={{  marginRight: '8px' }}/>
              <Box>
                <Typography variant="body1" sx={{ mt: 1 }}>
                  Launch date:
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                  21 May 2024
                </Typography>
              </Box>
            </Box>
          </Grid>

          <Grid item xs={6} sm={6} md={4} lg={2}>
            <Box
              sx={{
                border: '1px solid #ddd',
                padding: 2,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                borderRadius: 1,
                background: 'linear-gradient(to right,#53ae94, #40385d)',
                color: "black",
                textAlign: 'center',
                margin: "10px 5px"
              }}
            >
              <img src={GrowArrow} alt=""  style={{  marginRight: '8px' }}/>

              <Box>
                <Typography variant="body1" sx={{ mt: 1 }}>
                  Market Cap:
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                  $34,789.00
                </Typography>
              </Box>
            </Box>
          </Grid>

          <Grid item xs={6} sm={6} md={4} lg={2}>
            <Box
              sx={{
                border: '1px solid #ddd',
                padding: 2,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                borderRadius: 1,
                background: 'linear-gradient(to right, #655d65, #40385d)',
                color: "black",
                textAlign: 'center',
                margin: "10px 5px"
              }}
            >

            <img src={Buy} alt=""  style={{  marginRight: '8px' }}/>

              <Box>
                <Typography variant="body1" sx={{ mt: 1 }}>
                  Price:
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                  $7,540.00
                </Typography>
              </Box>
            </Box>
          </Grid>

          <Grid item xs={6} sm={6} md={4} lg={2}>
            <Box
              sx={{
                border: '1px solid #ddd',
                padding: 2,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                borderRadius: 1,
                background: 'linear-gradient(to right, #da8687, #40385d)',
                color: "black",
                textAlign: 'center',
                margin: "10px 5px"
              }}
            >

            <img src={Glob} alt=""  style={{  marginRight: '8px' }}/>

              <Box>
                <Typography variant="body1" sx={{ mt: 1 }}>
                  Circ.Supply:
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                  Circ.Supply
                </Typography>
              </Box>
            </Box>
          </Grid>

          <Grid item xs={6} sm={6} md={4} lg={2}>
            <Box
              sx={{
                border: '1px solid #ddd',
                padding: 2,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                borderRadius: 1,
                background: 'linear-gradient(to right, #c3d4ab, #40385d)',
                color: "black",
                textAlign: 'center',
                margin: "10px 5px"
              }}
            >



            <img src={Mans} alt=""  style={{  marginRight: '8px' }}/>

              <Box>
                <Typography variant="body1" sx={{ mt: 1 }}>
                  Holders:
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                  Holders
                </Typography>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Box>
      </Box>
    </div>
  );
};

export default Boxes;