import React from 'react';
import { Container, Typography, Box, Button, useTheme, useMediaQuery } from '@mui/material';
import rightImg from '../images/rightcolor.png';
import leftImg from '../images/leftcolor.png';
import Avator3 from '../images/avator1.png';
import Avator2 from '../images/avator2.png';
import Avator from '../images/avator3.png';

const Home = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Box sx={{ position: 'relative', width: '100%', overflow: 'hidden' }}>
      <img 
        src={leftImg} 
        alt="Left Background" 
        style={{
          position: 'absolute',
          top: '0px',
          left: '0',
          width: '95%', // Adjust size as needed
          height: '100%',
          zIndex: 1,
          opacity: 0.8 // Optional: adjust opacity for better text visibility
        }}
      />
      <img 
        src={rightImg} 
        alt="Right Background" 
        style={{
          position: 'absolute',
          top: '0',
          right: '0',
          width: '90%', // Adjust size as needed
          height: '100%',
          zIndex: 1,
          opacity: 0.8 // Optional: adjust opacity for better text visibility
        }}
      />

<img 
        src={leftImg} 
        alt="Left Background" 
        style={{
          position: 'absolute',
          top: '300px',
          left: '0',
          width: '95%', // Adjust size as needed
          height: '100%',
          zIndex: 1,
          opacity: 0.8 // Optional: adjust opacity for better text visibility
        }}
      />
      <img 
        src={rightImg} 
        alt="Right Background" 
        style={{
          position: 'absolute',
          top: '300px',
          right: '0',
          width: '90%', // Adjust size as needed
          height: '100%',
          zIndex: 1,
          opacity: 0.8 // Optional: adjust opacity for better text visibility
        }}
      />      
      <Container 
        maxWidth="lg" 
        sx={{ 
          pt: { xs: '30%', sm: '30%', md: '20%', lg: '10%' }, 
          textAlign: 'center', 
          py: 5, 
          textTransform: "capitalize",
          position: 'relative',
          zIndex: 2,
        }}
      >
        <img src={Avator} alt="Avator1" style={{position:"relative",width:"50px",marginRight:"75%",top:"100px"}} />
        <img src={Avator2} alt="Avator1" style={{position:"relative",width:"50px",marginRight:"15%",bottom:"30px"}} />
        <img src={Avator3} alt="Avator1" style={{position:"relative",width:"50px",top:"250px",left:"450px"}} />

        <Typography variant="h6" gutterBottom sx={{ fontSize: "15px", color: 'rgba(221, 221, 221, 1)', textAlign: { sm: 'left', xs: 'left', md: 'left', lg: 'center' } }}>
          Welcome to MINEX Extreme
        </Typography>
        <Typography variant="h6" sx={{ fontWeight: 'bold', lineHeight: "50px", fontSize: "50px", textAlign: { sm: 'left', xs: 'left', md: 'left', lg: 'center' } }} gutterBottom>
          Join the 
          <strong style={{color:"purple"}}>
            MINEX Revolution
          </strong> 
          and <br /> be part of a game-changing movement.
        </Typography>
        <Typography variant="body1" paragraph sx={{ textAlign: { sm: 'left', xs: 'left', md: 'left', lg: 'center' }, paddingTop: "20px", fontSize: "12px", color: 'rgba(221, 221, 221, 1)' }}>
          This Ethereum-based token offers unprecedented stability and value to holders, backed by a state-of-the-art Bitcoin mining farm.<br/>
          We are transforming the token industry by merging cutting-edge Bitcoin mining technology with tangible MINEX value.<br/>
          With $MINEX, we are opening new doors of opportunities for token holders, where utility meets viability.
        </Typography>
        <Box 
          sx={{ 
            display: 'flex', 
            justifyContent: 'center', 
            flexWrap: 'wrap', 
            gap: 2, 
            mt: 3, 
            flexDirection: isSmallScreen ? 'column' : 'row'
          }}
        >
          <Button 
            variant="contained" 
            color="primary"
            sx={{ 
              padding: "15px",
              mb: 1, 
              borderRadius: "40px", 
              backgroundColor: "purple",
              '&:hover': { backgroundColor: "purple" },
              width: isSmallScreen ? '45%' : 'auto' 
            }}
          >
            Join the Pre-Sale
          </Button>
          <Button 
            variant="outlined" 
            color="primary"
            sx={{ 
              padding: "15px",
              mb: 1, 
              borderRadius: "40px", 
              backgroundColor: "gray", 
              color: "black", 
              fontSize: "15px",
              '&:hover': { backgroundColor: "gray" },
              width: isSmallScreen ? '65%' : 'auto' 
            }}
          >
            Explore the Whitepaper
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default Home;
