import React from 'react';
import { makeStyles } from '@mui/styles';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import bitImage from '../images/bitsika.png';
import ethImage from '../images/ethereum.png'; 
import ltcImage from '../images/filecoin.png';
import tImage from '../images/tcoin.png'; 
import zImage from '../images/zcoin.png'; 
import BitImage from '../images/bit.png'; 
import BinanceImage from '../images/binance.png'; 

const useStyles = makeStyles((theme) => ({
  bitcoinContainer: {
    marginTop: "5%",
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-evenly',
    padding: '20px', 
    boxShadow: '0 4px 15px rgba(255,255, 255, 0.2)',
    '@media (max-width: 600px)': {
      justifyContent: 'center',
    },
  },
  bitcoinItem: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: '10px', 
    '@media (max-width: 600px)': {
      margin: '5px',
    },
  },
  bitcoinImage: {
    width: '50px',
    height: '50px',
    marginBottom: '10px',
    '@media (max-width: 600px)': {
      width: '30px',
      height: '30px',
    },
  },
  bitcoinTitle: {
    textAlign: 'center',
    color: '#fff',
    '@media (max-width: 600px)': {
      fontSize: '10px',
    },
  },
}));

const coins = [
  { name: 'Bitcoin', image: bitImage },
  { name: 'Ethereum', image: ethImage },
  { name: 'Litecoin', image: ltcImage },
  { name: 'Tcoin', image: tImage },
  { name: 'Zcoin', image: zImage },
  { name: 'Bit', image: BitImage },
  { name: 'Binance', image: BinanceImage },
];

function BitcoinComponent() {
  const classes = useStyles();

  return (
    <>
      <Typography style={{ marginTop: "20%", fontSize: "14px", alignItems: "center", textAlign: "center" }}>
        *For more information,
        <a href="" style={{ textDecoration: "none", color: "purple" }}>Check the whitepaper
          <i className="fa-solid fa-arrow-right-long" style={{ marginLeft: "10px", fontSize: "15px" }}></i></a>
      </Typography>
      <Typography style={{ marginTop: "4%", fontSize: "30px", alignItems: "center", textAlign: "center" }}>
        Strategic Partnership
      </Typography>
      <Box className={classes.bitcoinContainer}>
        {coins.map((coin, index) => (
          <Box key={index} className={classes.bitcoinItem}>
            <img
              src={coin.image}
              alt={coin.name}
              className={classes.bitcoinImage}
            />
            <Typography className={classes.bitcoinTitle}>{coin.name}</Typography>
          </Box>
        ))}
      </Box>
    </>
  );
}

export default BitcoinComponent;
